html {
  box-sizing: border-box;
  -ms-overflow-style: none;
}

::selection {
  text-shadow: none;
  color: #ffffff;
  background-color: #0d568f;
}

*:focus {
  outline: none;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  -ms-overflow-style: none;
}

html,
body {
  font-family: "roboto";
  font-size: 18px;
  font-weight: 300;
  margin: 0px;
  padding: 0px;
  outline: none;
  color: #333333;
  height: 100%;
  min-height: 100%;
}

body {
  line-height: 1.5;
  overflow-x: hidden;
  color: #333333;
  font-size: 90%;
}

@media only screen and (min-width: 760px) {
  body {
    font-size: 95%;
  }
}

@media only screen and (min-width: 960px) {
  body {
    font-size: 100%;
  }
}

@media only screen and (min-width: 1200px) {
  body {
    font-size: 100%;
  }
}

em {
  font-style: italic;
}

strong {
  font-weight: 700;
}

