.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.fa-6x {
  font-size: 6em;
}

.fa-7x {
  font-size: 7em;
}

.fa-8x {
  font-size: 8em;
}

.body-copy a,
.editor-preview-active a,
.editor-preview-active-side a, .team-member-bio a {
  border-bottom: 0px solid #0d568f;
  box-shadow: inset 0 -0.1em 0 #0d568f;
  color: #333333;
  transition: background 0.3s cubic-bezier(0.33, 0.66, 0.66, 1);
  text-decoration: none;
}

.body-copy a:hover,
.editor-preview-active a:hover,
.editor-preview-active-side a:hover, .team-member-bio a:hover {
  transition: background 0.3s cubic-bezier(0.33, 0.66, 0.66, 1);
  background: #0d568f;
  color: #ffffff;
}

a#bigbutton {
  box-shadow: none;
  display: block;
}

a#bigbutton:hover {
  background-color: transparent;
}

a.image-link {
  border: 0px;
  box-shadow: none !important;
}

a.image-link:hover {
  background: none !important;
}

li a {
  display: inline;
}

.audio-player {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  max-width: 324px;
  display: block;
  flex-direction: column;
  border: 1px solid #cccccc;
  border-radius: 2px;
  margin-bottom: 1em;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.audio-player .audio-info {
  display: flex;
}

.audio-player .audio-thumbnail {
  width: 100px;
  height: 100px;
  max-height: 100px;
}

.audio-player .audio-meta {
  display: flex;
  flex-direction: column;
  position: relative;
  justify-content: center;
  width: calc(100% - 100px);
  padding-left: .5em;
}

.audio-player .audio-meta .audio-title {
  font-size: 1.3em;
  line-height: 1;
}

.audio-player .audio-meta .audio-artist {
  font-size: .7em;
  margin-top: 0em;
  font-weight: 700;
  color: gray;
}

.audio-player .audio-meta .itunes-link {
  box-shadow: none;
  border-bottom: none;
  background-color: transparent;
  position: absolute;
  bottom: .25em;
  right: .5em;
}

.audio-player .audio-meta .itunes-link:hover {
  box-shadow: none;
  border-bottom: none;
  background-color: transparent;
  color: #333333;
}

.audio-player .audio-meta .itunes-link:before, .audio-player .audio-meta .itunes-link:after {
  box-shadow: none;
  border-bottom: none;
  background-color: transparent;
}

.audio-player audio.audio-file {
  width: 100%;
  background-color: #0d568f;
}

/***********ICONS STYLES****************/
.next:after,
.button.next:after,
a.next:after,
button.next:after {
  content: "\F101";
  font-family: "fontello";
  margin-left: .5em;
}

.next:hover::after,
.button.next:hover::after,
a.next:hover::after,
button.next:hover::after {
  transition: margin 0.3s ease-in-out;
  margin-left: .75em;
}

.button.prev:before,
a.prev:before {
  content: '\F100';
  font-family: "fontello";
  margin-right: .5em;
}

.button.prev:hover::before,
a.prev:hover::before {
  transition: margin 0.3s ease-in-out;
  margin-right: .75em;
}

.close {
  text-align: center;
  display: block;
}

.close:after {
  display: block;
  margin-right: auto;
  margin-left: auto;
  display: block;
  content: 'CLOSE';
  font-size: .3em;
  margin-top: -1em;
}

h1 i[class^="icon-"]:before,
h2 i[class^="icon-"]:before,
h3 i[class^="icon-"]:before,
h4 i[class^="icon-"]:before {
  margin-left: 0px;
  margin-right: .15em;
}

h1.title i.icon-content-owners:before {
  margin-right: 0px;
  margin-left: 0em;
  font-size: 1.2em;
  vertical-align: bottom;
}

h1.title i.icon-content-owners-list:before {
  margin-right: 0px;
}

h1.title i.icon-site-updates:before {
  margin-right: 0em;
