}

h1.title i.icon-submit-content:before {
  margin-left: 0em;
  margin-right: 0em;
}

h1.title i.icon-docs:before {
  margin-left: 0em;
  margin-right: 0em;
}

h1.title i.icon-digital-writers-guide:before {
  margin-left: 0em;
