.scroll-down {
  display: block;
  width: 100%;
  position: absolute;
  margin-bottom: 0px;
  left: 0px;
  text-align: center;
  z-index: 10;
  animation: lookdown 900ms infinite;
}

.scroll-down a#scroll-down {
  color: #ffffff;
  opacity: .7;
}

.scroll-down a#scroll-down i.icon-down:before {
  font-size: 1.5em;
  font-weight: 100;
}

@media only screen and (min-width: 760px) {
  .scroll-down {
    bottom: 0px;
    margin-bottom: 0px;
  }
}

.icon-scroll.in-search-results {
  position: absolute;
  bottom: 4px;
  right: 4px;
  animation: lookdown 900ms infinite;
  z-index: 99;
  display: none;
  font-size: 2em;
  font-weight: bold;
  color: #0d568f;
}

.icon-scroll.in-search-results:before {
  margin-right: 0px;
}

@keyframes lookdown {
  0% {
    bottom: 0px;
  }
  30% {
    bottom: 6px;
  }
  50% {
    bottom: 2px;
  }
  100% {
    bottom: 0px;
  }
}

i.icon-next {
  position: absolute;
  right: 10px;
}

i.icon-next:before {
  transition: all .3s ease-in-out;
  transform: rotate(0deg);
}

i.icon-next.open::before {
  transition: all .3s ease-in-out;
