  transform: scale(0.9) !important;
  background: #45a142;
  background-color: #45a142;
  top: 0px !important;
}

#google-form-wrapper {
  transition: transform .3s ease;
  width: 100%;
  height: 100%;
  display: block;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 50px;
  right: 0px;
  z-index: 99;
  background-color: #333333;
  max-width: 500px;
  transform: translateY(100%);
  padding-top: 1.5em;
  overflow-y: scroll;
}

#google-form-wrapper .form-heading {
  display: block;
  width: 100%;
  text-align: center;
  font-size: 1.4em;
  color: #ffffff;
  padding: .25em .5em;
  max-width: 80%;
  margin-right: auto;
  margin-left: auto;
  border: 3px solid #ffffff;
  z-index: 20;
  position: relative;
}

#google-form-wrapper.form-open {
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  transition: transform .3s ease;
  transform: translateY(0%);
  overflow-y: scroll;
}

@media only screen and (min-width: 760px) {
  #google-form-wrapper {
    transform: translateY(0%);
    transform: translateX(100%);
  }
  #google-form-wrapper.form-open {
    transform: translateX(0%);
  }
}

a#open-google-form {
  position: fixed;
  bottom: 35px;
  right: 0px;
  height: 60px;
  width: 60px;
  padding: 2px;
  z-index: 10;
  background-color: #a42f36;
  display: block;
  color: #ffffff;
  line-height: 1.05;
  font-size: .8em;
  font-weight: 400;
}

a#open-google-form i {
  font-size: 1.6em;
  display: block;
  margin-bottom: .25em;
}

a#open-google-form > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 60px;
  text-align: center;
}

@media only screen and (min-width: 760px) {
  a#open-google-form > div {
    min-height: 70px;
  }
}

a#open-google-form div > span {
  font-size: 12px;
  line-height: 1;
  text-align: center;
}

@media only screen and (min-width: 760px) {
  a#open-google-form div > span {
    font-size: 14px;
  }
}

@media only screen and (min-width: 760px) {
  a#open-google-form {
    height: 70px;
    width: 70px;
    top: 50px;
    right: 0px;
    left: auto;
  }
}

a#close-google-form {
  position: fixed;
  top: .3em;
  right: .3em;
  font-size: 1.2em;
  color: #ffffff;
  z-index: 99;
}

.inner-form-wrapper {
  height: auto;
  min-height: 100vh;
  position: relative;
  display: block;
  margin-right: auto;
  margin-left: auto;
  max-width: 88%;
