  transform: translateY(6px) rotate(0deg);
}

.burger-container.menu-open {
  transform: rotate(90deg);
}

.burger-container.menu-open #burger .bar {
  transition: all 0.4s cubic-bezier(0.4, 0.01, 0.165, 0.99);
  transition-delay: 0.2s;
}

